.Modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 70%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 50%;
  top: 50%;
  border-radius: 0.5rem;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  max-height: 96vh;
  overflow-y: auto;
}

@media (min-width: 600px) {
  .Modal {
    width: 50%;
  }
}
